    text-align: center;
    padding: 0 0 30px 0;
  }

  .about h1{
    font-size: 2.3rem;
  }

  .about h3{
    font-size: 1.2rem;
  }

  .about p{
    font-size: .9rem;
    padding: 0 60px;
  }

  .about-rows{
    padding-bottom: 40px;
  }

  .about-display{
    padding: 0 20px;
  }

  .about-display img{
    width: 100%;
  }


  .product-section{
    width: 100%;
    margin: 50px 0;
  }

  .product-heading{
    height: auto;
    padding: 0;
  }

  .product-title{
    margin: 0 auto;
    padding: 20px 0;
  }

  .product-more p,
  .product-more img{
