    padding: 0 10px;
  }

  .footer-col-1 p{
    padding: 0 40px 20px;
  }

  .footer-col h3{
    text-align: center;
  }

  .footer-col li{
    text-align: center;
  }

}



@media (max-width: 460px){
  .feedback-section{
    margin-bottom: 40px;
    padding-bottom: 120px;
  }

  .footer-section{
    padding: 60px 0 120px;
  }

}
